html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: "Manrope", sans-serif;
  font-size: 2.8rem;
  background-color: hsl(218deg, 23%, 16%);
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.container {
  width: min(97%, 120rem);
  margin-inline: auto;
}
@media (min-width: 1440px) {
  .container {
    width: min(35%, 120rem);
  }
}

.advice {
  background-color: hsl(217deg, 19%, 24%);
  color: hsl(193deg, 38%, 86%);
  border-radius: 1rem;
  position: relative;
  padding-bottom: 6rem;
  margin-top: -30rem;
}
@media (min-width: 1440px) {
  .advice {
    margin-top: 0;
  }
}
.advice__number {
  color: hsl(150deg, 100%, 66%);
  font-size: 1.5rem;
  letter-spacing: 2px;
  text-align: center;
  padding-top: 2rem;
}
.advice__container {
  text-align: center;
}
.advice__border {
  display: flex;
  margin-top: -2.5rem;
  justify-content: center;
}
.advice__btn {
  cursor: pointer;
  background-color: hsl(150deg, 100%, 66%);
  position: absolute;
  padding: 1rem 1.5rem;
  border-radius: 50%;
  bottom: -3rem;
  left: 50%;
  transform: translateX(-50%);
}
.advice__btn:hover, .advice__btn:active {
  box-shadow: 2px -2px 30px hsl(150deg, 100%, 66%);
}
.advice__btn:hover img {
  animation: rotate 1s ease-in infinite;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg) scale(1.1);
  }
}

.attribution {
  font-size: 1.2rem;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  color: hsl(193deg, 38%, 86%);
}